<template>
  <div id="page" :class="$store.state.activeMenu + '-page'">
    <Headernav></Headernav>
    <div class="container">
      <transition name="router-fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
    <Bottomnav></Bottomnav>
  </div>
</template>

<script>
import Bottomnav from './pages/components/bottom'
import Headernav from './pages/components/nav'

export default {
  components: { Bottomnav, Headernav },
  data(){
    return {
    }
  },
  mounted: function () {
    //js调整fontsize
    var width = 480;
    if (document.documentElement.clientWidth < 480) {
      width = document.documentElement.clientWidth;
    }
    document.documentElement.style.fontSize = width/20 + 'px';
  },
  methods:{
    
  }
}

</script>

<style>

</style>



