<template>
<div>
    <div class="bottom-nav">
        <a class="tabbar-index" :class="{'tabbar-index_cur': $store.state.activeMenu == 'home'}" href="/#/">首页</a>

        <a class="tabbar-list" href="/#/list" :class="{'tabbar-list_cur': $store.state.activeMenu == 'list'}">所有商品</a>
        <a class="tabbar-cart" href="/#/cart" :class="{'tabbar-cart_cur': $store.state.activeMenu == 'cart'}">购物车
        <span class="nav-cart-num " style="display: block;">{{$store.state.cartcount}}</span></a>

        <a class="tabbar-member" href="/#/my" :class="{'tabbar-member_cur': $store.state.activeMenu == 'usercenter'}">我的</a>
    </div>

    <footer class="touch-footer-container" style="transform-origin: 0px 0px 0px; opacity: 1;">
        <ul class="tab clear">
            <li class="checked"><a href="//m.guyushi.com">触屏版</a></li>
            <li class=""><a href="http://www.guyushi.com/">电脑版</a></li>
        </ul>
        <div class="bottom-rights">
            <p>©2017 故与事 guyushi.com</p>
        </div>
    </footer>
    <a class="goto-top block" v-if="$store.state.showGoTop" @click="gotop"></a>


  </div>
</template>
<script type="text/javascript">
  export default{
    data(){
      return {
      }
    }, 
    mounted(){
        this.$store.commit("init", null);
    },
    methods:{
      gotop: function(){
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        return false;
      }
    }
  }
</script>
<style>
.bottom-nav {
    width: 20rem;
    height: 3.0625rem;
    position: fixed;
    z-index: 1;
    left: 0;
    bottom: 0;
    text-align: center;
    box-sizing: border-box;
    line-height: normal;
    background-color: rgb(255, 255, 255); border-top: 1px solid rgb(238, 238, 238);
}.bottom-nav a {
    display: inline-block;
    width: 4.5rem;
    height: 3.0621rem;
    color: #999;
    background-repeat: no-repeat;
    background-position: center .35116rem;
    background-size: 1.4063rem 1.2539rem;
    box-sizing: border-box;
    padding-top: 1.7578rem;
    text-decoration: none;
    font-family: Arial,helvetica,"微软雅黑","Microsoft Yahei",sans-serif;
    color: rgb(153, 153, 153);
}
.bottom-nav a.tabbar-index{
    background-image: url("../../assets/imgs/home.png");
}
.bottom-nav a.tabbar-index_cur{
    background-image: url("../../assets/imgs/home_cur.png");
    color: rgb(254, 64, 112);
}
.bottom-nav a.tabbar-list{
    background-image: url("../../assets/imgs/product.png");
}
.bottom-nav a.tabbar-list_cur{
    background-image: url("../../assets/imgs/product_cur.png");
    color: rgb(254, 64, 112);
}
.bottom-nav a.tabbar-cart{
    background-image: url("../../assets/imgs/shoppingcart.png");
}
.bottom-nav a.tabbar-cart_cur{
    background-image: url("../../assets/imgs/shoppingcart_cur.png");
    color: rgb(254, 64, 112);
}
.bottom-nav a.tabbar-member{
    background-image: url("../../assets/imgs/usercenter.png");
}
.bottom-nav a.tabbar-member_cur{
    background-image: url("../../assets/imgs/usercenter_cur.png");
    color: rgb(254, 64, 112);
}

.nav-cart-num {
    display: block;
    position: absolute;
    width: .8749rem;
    height: .8749rem;
    line-height: .8555rem;
    top: .2344rem;
    left: 50%;
    margin-left: .4106rem;
    font-size: .5625rem;
    border: solid 1px #fff;
    text-align: center;
    color: #fff;
    background-color: #fe4070;
    font-family: Arial,helvetica,"微软雅黑","Microsoft Yahei",sans-serif;
    box-sizing: content-box;
}.bottom-nav a.tabbar-cart {
    position: relative;
}.nav-cart-num {
    border-radius: 100px;
}

.touch-footer-container .tab {
    text-align: center;
    width: 18.75rem;
    margin: 10px auto 0;
}.touch-footer-container .tab .checked {
    background: #FFF;
}.touch-footer-container .tab li {
    border: 1px solid #e3e3e3;
    line-height: 31px;
    color: #666;
    font-size: 14px;
    height: 31px;
    width: 4.48rem;
    margin-right: 2.293rem;
    display: inline-block;
    border-radius: 1.25rem;
}.touch-footer-container .tab .checked a {
    color: #fe4070;
}.touch-footer-container .bottom-rights {
    position: relative;
    margin: 1.25rem auto .625rem;
    color: #999;
    font-size: 16px;
    width: 100%;
    text-align: center;
}

</style>



