
<template>
  <div v-if="$store.state.showMenu">
    <!-- 顶部 -->
    <header class="gys-header" ref="headerdiv">
        <a class="home_log" href="/#/my"><img class="my" src="//f0.jmstatic.com/btstatic/h5/common/login2.png"></a>
        <a id="page_top" href="/#/search" class="index-search">
          <img src="//f0.jmstatic.com/btstatic/h5/common/search_btn.png"><span>搜索商品 分类 功效</span>
        </a>
        <a href="/#/search">
          <span id="search_action"><img class="search" src="//f0.jmstatic.com/btstatic/h5/index/search_list2.png"></span>
        </a>
    </header>
    <!-- 顶部菜单 -->
    <div id="hearder_nav">
        <div id="nav-outer" :class="{'fix-menu': isShowFixMenu}">
            <div class="headerNav clearfix" id="nav-inner">
                <p class="navTitle nt_deal" :class="{'nav_select': $store.state.activeMenu=='home'}">
                    <a href="/#/">
                        <span>首页</span>
                        <span class="header-nav-line"></span>
                    </a>
                </p>
                <p class="navTitle nt_deal" :class="{'nav_select': $store.state.activeMenu=='list'}">
                    <a href="/#/list">
                        <span>商品列表</span>
                        <span class="header-nav-line"></span>
                    </a>
                </p>
                <p class="navTitle nt_deal">
                    <router-link  to="/login">
                    <span>限时秒杀</span>
                    <span class="header-nav-line"></span>
                    </router-link >
                </p>
                <p class="navTitle nt_deal">
                    <router-link to="/my">
                        <span>我的账号</span>
                        <span class="header-nav-line"></span>
                    </router-link >
                </p>
            </div>
        </div>
    </div>
</div>
</template>
<script>
  export default {
    data(){
      return {
        isShowFixMenu:false
      }
    },
    methods: {
      handleScroll() {
        if (this.$store.state.showMenu) {
          this.isShowFixMenu = window.scrollY > this.$refs.headerdiv.getBoundingClientRect().height;
        }

        var scrollValue = document.body.scrollTop || document.documentElement.scrollTop;
        
        if(scrollValue > 100){
            if (!this.$store.state.showTop) {
                this.$store.commit("changeTop", true)
            }
        } else
          this.$store.commit("changeTop", false)

      }
    },
    mounted () {
        window.addEventListener('scroll', this.handleScroll);
    }
  }
</script>
<style>
#position, .float_car, .go_top, .index-activity-title div, .loading, .mall_sel, .navTitle, .sell_state, header #page_top {
    text-align: center;
}
  .gys-header{
    background: #fff;
    overflow: hidden;
    position: relative;
  }
  .gys-header h1{
    visibility: hidden;
  }
  .gys-header .home_log {
    position: absolute;
    left: .96rem;
    bottom: .64rem;
    height: 1.067rem;
    width: .96rem;
  }header .my,header .search {
    width: 100%;
}header #page_top {
    margin: .4375rem 2.88rem .347rem;
    color: #999;
    background: #F5F5F5;
    border-radius: 1.25rem;
    height: 1.653rem;
    line-height: 1.653rem;
    position: relative;
    display: block;
    box-shadow: 0 0.0625rem 0.125rem rgba(0,0,0,.05) inset;
}header #page_top img {
    width: .875rem;
    display: inline-block;
    position: relative;
    top: .1875rem;
    left: -.1875rem;
}#search_action {
    width: 1.28rem;
    height: .987rem;
    position: absolute;
    bottom: .68rem;
    right: .64rem;
    z-index: 12;
}
#hearder_nav {
    height: 2.347rem;
    position: relative;
    z-index: 20;
}.headerNav {
    height: 2.347rem;
    background-color: #fff;
}.navTitle {
    float: left;
    position: relative;
    width: auto;
    padding: 0 .9375rem;
}.navTitle>a {
    padding: 0 .125rem;
    height: 2.375rem;
    line-height: 2.375rem;
    display: inline-block;
    width: auto;
}.headerNav .navTitle a {
    color: #666;
    text-decoration: none;
    position: relative;
    -webkit-tap-highlight-color: transparent;
}.headerNav .nav_select>a {
    color: #fe4070;
    border-bottom: none;
}
.nav_select .header-nav-line {
    background: #fe4070;
    height: .125rem;
    border-radius: .125rem .125rem 0 0;
    display: block;
    position: absolute;
    bottom: 0;
    width: 100%;
    left: -.0625rem;
}.navTitle a img, .navTitle span {
    vertical-align: middle;
}
.fix-menu {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 18;
    width: 100%;
}

</style>



