import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const AUTH_JWT_TOKEN = 'auth.jwt_token';
const AUTH_USER = 'auth.user';
const AUTH_USER_ID = 'auth.id';

//
const CART_LIST = "cart.list";
const CART_COUNT = "cart.count";
const CART_TOTAL = "cart.total";

const localStorage = global.localStorage;

export default new Vuex.Store({
  state: {
    //显示头部
    showMenu: true,
    //当前菜单项
    activeMenu: 'home',
    //显示回到顶部
    showGoTop:false,
    //图片链接
    domainUrl:'http://www.guyushi.com/',
    //api链接
    apidomain:'http://www.guyushi.com/',
    //登录
    auth: {
      check() {
        return this.jwt_token !== null;
      },
      jwt_token: localStorage.getItem(AUTH_JWT_TOKEN),
      id: localStorage.getItem(AUTH_USER_ID),
      user: JSON.parse(localStorage.getItem(AUTH_USER)),
    },

    //cart
    cartcount: 0,
    //id, count, name, price, sku , check
    cartlist: []
  },
  mutations: {
    //修改回到顶部显示或隐藏
    changeMenu (state, value) {
      state.showMenu =  value;
    },
    //修改菜单显示或隐藏
    changeTop (state, value) {
      state.showGoTop =  value;
    },
    //修改菜单当前项
    changeNav(state, value){
      state.activeMenu =  value;
    },
    //登录状态改变
    ACCOUNT_AUTH_STATUS_CHANGED(state,  data) {
      if (!data) {
        Vue.set(state.auth, 'jwt_token', null);
        Vue.set(state.auth, 'id', 0);
        Vue.set(state.auth, 'user', null);
        localStorage.removeItem(AUTH_JWT_TOKEN);
        localStorage.removeItem(AUTH_USER_ID);
        localStorage.removeItem(AUTH_USER);
        return;
      }
      
      Vue.set(state.auth, 'jwt_token', data.jwt_token);
      Vue.set(state.auth, 'id', data.id);
      Vue.set(state.auth, 'user', data);
      localStorage.setItem(AUTH_JWT_TOKEN, data.jwt_token);
      localStorage.setItem(AUTH_USER_ID, data.id);
      localStorage.setItem(AUTH_USER, JSON.stringify(data));
    },
    //修改title
    changeTitle(state, data) {
      document.title = data + " | 故与事-一家有实体店的化妆品商城";
    },
    //shoppingcart
    init(state, data){
      state.cartcount = parseInt(localStorage.getItem(CART_COUNT) || 0);
      state.cartlist = JSON.parse(localStorage.getItem(CART_LIST)) || [];
    },
    //add item to cart
    addCart(state,data){
      state.cartcount  = 0;
      state.carttotal = 0;
      var isalreadyincart = false;
      for (var i = state.cartlist.length - 1; i >= 0; i--) {
        if(state.cartlist[i].id == data.id){
          state.cartlist[i].count++;
          isalreadyincart = true;
        }
        state.cartcount += parseInt(state.cartlist[i].count);
      }
      if (!isalreadyincart) {
        data.check = true;
        state.cartlist.push(data);
        state.cartcount += parseInt(data.count);
      }

      localStorage.setItem(CART_COUNT, state.cartcount);
      localStorage.setItem(CART_LIST, JSON.stringify(state.cartlist));
    },
    //del item from cart
    delCart(state,data){
      state.cartcount  = 0;
      state.carttotal = 0;
      for (var i = state.cartlist.length - 1; i >= 0; i--) {
        if(state.cartlist[i].id == data){
          state.cartlist.splice(i, 1);
          continue;
        }

        state.cartcount += parseInt(state.cartlist[i].count);
      }
      
      localStorage.setItem(CART_COUNT, state.cartcount);
      localStorage.setItem(CART_LIST, JSON.stringify(state.cartlist));
    },
    //update item 
    updateCart(state,data) {
      state.cartcount  = 0;
      state.carttotal = 0;
      for (var i = state.cartlist.length - 1; i >= 0; i--) {
        if(state.cartlist[i].id == data.id){
          state.cartlist[i].count = parseInt(data.count);
        }

        state.cartcount += parseInt(state.cartlist[i].count);
      }

      localStorage.setItem(CART_COUNT, state.cartcount);
      localStorage.setItem(CART_LIST, JSON.stringify(state.cartlist));
    },
    //update localStorage cart from state.cartlist,
    //use for shopping cart to update check status
    SyncLocalStorageCart(state, data){
      localStorage.setItem(CART_LIST, JSON.stringify(state.cartlist));
    }

  }
})


