
const Home = r => require.ensure([], () => r(require('../pages/index/home')), 'Home')
const Login = r => require.ensure([], () => r(require('../pages/login/index')), 'Login')
const Search = r => require.ensure([], () => r(require('../pages/products/search')), 'Product')
const Productlist = r => require.ensure([], () => r(require('../pages/products/productlist')), 'Product')
const ProductCategory = r => require.ensure([], () => r(require('../pages/products/category')), 'Product')
const ProductDetail = r => require.ensure([], () => r(require('../pages/products/detail')), 'Product')


//购物车
const shoppingCart = r => require.ensure([], () => r(require('../pages/cart/shoppingCart')), 'Order')
const submitorder = r => require.ensure([], () => r(require('../pages/order/comfirm')), 'Order')

//用户中心
const My = r => require.ensure([], () => r(require('../pages/user/index')), 'My')
const OrderList = r => require.ensure([], () => r(require('../pages/user/order/list')), 'Order')



export default [
  {
    path : '/',
    component : Home,
    meta: {noCheckSession: true},
    name:"home"
  },

  {
    path : '/home',
    component : Home,
    meta: {noCheckSession: true}
  },

  {
    path:'/list',
    component:Productlist,
    meta: {noCheckSession: true}
  },
  {
    path:'/search',
    component:Search,
    meta: {noCheckSession: true}
  },
  {
    path:'/search/:keywords',
    component:Search,
    meta: {noCheckSession: true}
  },
  {
    path:'/p/:productid',
    component:ProductDetail,
    name:'ProductDetail',
    meta: {noCheckSession: true}
  },
  //category
  {
    path:'/category',
    component: ProductCategory,
    name:'ProductCategory',
    meta:{noCheckSession:true}
  },
  //shoppingcar
  {
    path:'/cart',
    component: shoppingCart,
    name:'shoppingcart',
    meta:{noCheckSession:true}
  },
  //submit order
  {
    path:'/submit/:itemid',
    component: submitorder,
    name:'submitOrderSingle'
  },
  //submit order
  {
    path:'/submit',
    component: submitorder,
    name:'submitOrderFromCart'
  },

  //login
  {
    path:'/login',
    component:Login,
    meta: {noCheckSession: true},
    name:"login"
  },

  //reg
  {
    path:'/reg',
    component:Home,
    meta: {noCheckSession: true},
    name:"reg"
  },


  //user center
  {
    path:'/my',
    component:My,
    name:"my"
  },
  {
    path: '/order',
    component: OrderList,
    name: 'orderlist' 
  },
  {
    path: '/orderdetail/:orderid',
    component: OrderList,
    name:'orderdetail'
  }
];


